.button_theme_gradient {
  background: #cf5658;
  background: -webkit-linear-gradient(left, #cf5658 2%, #9b57de 100%);
  background: -o-linear-gradient(left, #cf5658 2%, #9b57de 100%);
  background: linear-gradient(to right, #cf5658 2%, #9b57de 100%);
  font-family: 'Catamaran', sans-serif;
  position: relative;
  display: inline-block;
  width: 250px;
  padding: 10px;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  color: #fff;
  border-radius: 80px;
  outline: 0;
  text-align: center;
  white-space: nowrap;
  -webkit-tap-highlight-color: transparent;
}
