.button_theme_island {
  border: 5px solid #000;
  font-family: 'Catamaran', sans-serif;
  position: relative;
  display: inline-block;
  width: 250px;
  padding: 10px;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  color: #000;
  border-radius: 80px;
  outline: 0;
  text-align: center;
  white-space: nowrap;
  -webkit-tap-highlight-color: transparent;
}
.button_theme_island .button__text {
  color: #cf5658;
}
