.grid__template_3_4 {
  display: grid;
  width: 100%;
  grid-template-areas: "header header header" "subtitle subtitle subtitle" "main main main" "spacer spacer spacer";
  grid-template-rows: 150px 140px 1fr 150px;
  align-items: center;
}
.grid__template_3_4 .header {
  grid-area: header;
}
.grid__template_3_4 .subtitle {
  grid-area: subtitle;
}
.grid__template_3_4 .main {
  grid-area: main;
}
.grid__template_3_4 .spacer {
  grid-area: spacer;
}
@media only screen and (max-width: 1600px) {
  .grid__template_3_4 {
    grid-template-columns: 0fr 4fr 2fr;
  }
}
@media only screen and (max-width: 1390px) {
  .grid__template_3_4 {
    grid-template-columns: 0fr 4fr 2fr;
  }
}
