.line__el {
  position: relative;
  margin-left: 70%;
  background: #495d79;
  height: 4px;
  width: 350px;
  border-radius: 90px;
}
.line__el span {
  top: -10.5px;
  border-radius: 50%;
  position: absolute;
  right: -22px;
  width: 25px;
  height: 25px;
  border: 4px solid #495d79;
}
@media only screen and (max-width: 1600px) {
  .line__el {
    margin-left: 50%;
  }
}
@media only screen and (max-width: 1390px) {
  .line__el {
    margin-left: 30%;
  }
}
