.switch {
  position: relative;
  display: inline-block;
  margin: 0 auto;
  width: 60px;
  height: 34px;
}
.switch input {
  opacity: 0;
  width: 0;
  height: 0;
}
.button_theme_switch {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #495d79;
  -webkit-transition: 0.4s;
  transition: 0.4s;
}
.button_theme_switch:before {
  position: absolute;
  content: "";
  height: 26px;
  width: 26px;
  left: 4px;
  bottom: 4px;
  background-color: #fff;
  -webkit-transition: 0.4s;
  transition: 0.4s;
}
.button_theme_switch.round {
  border-radius: 34px;
}
.button_theme_switch.round:before {
  border-radius: 50%;
}
input:checked + .button_theme_switch {
  background-color: #cf5658;
}
input:checked + .button_theme_switch:before {
  -webkit-transform: translateX(26px);
  -ms-transform: translateX(26px);
  transform: translateX(26px);
}
input:focus + .button_theme_switch {
  box-shadow: 0 0 1px #cf5658;
}
