.fadeInLeft {
  -webkit-animation-name: fadeInLeft;
  animation-name: fadeInLeft;
}
.fadeInRight {
  -webkit-animation-name: fadeInRight;
  animation-name: fadeInRight;
}
.fadeUpIn {
  -webkit-animation-name: fadeUpIn;
  animation-name: fadeUpIn;
}
@-webkit-keyframes fadeUpIn {
  0% {
    opacity: 0;
    -webkit-transform: translateY(0);
    -moz-transform: translateY(0);
    -o-transform: translateY(0);
    transform: translateY(0);
  }
  100% {
    opacity: 1;
    -webkit-transform: translateY(100%);
    -moz-transform: translateY(100%);
    -o-transform: translateY(100%);
    transform: translateY(100%);
  }
}
.header__m {
  font-size: 64px;
  font-family: 'Catamaran';
  font-weight: 100;
  color: #495d79;
  width: 1300px;
  margin: 0 auto;
}
.grid .header__m:nth-child(2) {
  -webkit-animation: fadeInRight 2.5s cubic-bezier(0, 1, 0.5, 1) 1 normal forwards;
  -moz-animation: fadeInRight 2.5s ease-in 1 normal forwards;
  -o-animation: fadeInRight 2.5s cubic-bezier(0, 1, 0.5, 1) 1 normal forwards;
  animation: fadeInRight 2.5s cubic-bezier(0, 1, 0.5, 1) 1 normal forwards;
}
@media only screen and (max-width: 1440px) {
  .header__m {
    width: 85%;
  }
}
@media only screen and (max-width: 1390px) {
  .header__m {
    width: 85%;
  }
}
