.grid__template_2_3 {
  display: grid;
  width: 100%;
  grid-template-areas: "l r" "spacer spacer";
  grid-template-rows: 1fr 150px;
  grid-template-columns: 50% 1fr;
}
.grid__template_2_3 .grid__block_l {
  grid-area: l;
}
.grid__template_2_3 .grid__block_r {
  grid-area: r;
}
.grid__template_2_3 .spacer {
  grid-area: spacer;
}
.grid__template_2_3 .item {
  grid-column: 3/2;
}
@media only screen and (max-width: 1800px) {
  .grid__template_2_3 {
    grid-template-columns: 55% 2fr;
  }
}
@media only screen and (max-width: 1600px) {
  .grid__template_2_3 {
    grid-template-columns: 65% 35%;
  }
}
@media only screen and (max-width: 1390px) {
  .grid__template_2_3 {
    grid-template-columns: 70% 30%;
  }
}
@media only screen and (max-width: 992px) {
  .grid__template_2_3 {
    grid-template-columns: 4fr 2fr;
  }
}
