.grid__template_1_3 {
  display: grid;
  width: 100%;
  grid-template-areas: "burger ul app";
  grid-template-rows: 220px;
  grid-template-columns: 2fr 4fr 2fr;
  align-items: center;
}
.grid__template_1_3 .burger {
  grid-area: burger;
}
.grid__template_1_3 ul {
  grid-area: ul;
}
.grid__template_1_3 .get {
  grid-area: app;
}
@media only screen and (max-width: 1800px) {
  .grid__template_1_3 {
    grid-template-columns: 0.5fr 4fr 2fr;
  }
}
@media only screen and (max-width: 1600px) {
  .grid__template_1_3 {
    grid-template-columns: 0.5fr 4fr 2fr;
  }
}
@media only screen and (max-width: 1440px) {
  .grid__template_1_3 {
    grid-template-columns: 0.5fr 3fr 1fr;
  }
}
@media only screen and (max-width: 1200px) {
  .grid__template_1_3 {
    grid-template-columns: 0.5fr 5fr 1fr;
  }
}
