.button_theme_buy {
  border: 5px solid #000;
  width: 250px;
  font-family: 'Catamaran', sans-serif;
  position: relative;
  display: inline-block;
  padding: 10px;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  color: #cf5658;
  border-radius: 80px;
  outline: 0;
  text-align: center;
  white-space: nowrap;
  -webkit-tap-highlight-color: transparent;
}
.button_theme_buy .button__text:hover {
  text-decoration: underline;
}
